@import url(https://fonts.googleapis.com/css?family=Noto+Sans+JP&display=swap);
.style_controls__b5iHy {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100px;
  background-color: #116;
}

button {
  display: inline-block;
  min-width: 240px;
  border: 0;
  padding: 8px 16px;
  margin-left: 4px;
  margin-right: 4px;
  border-radius: 3px;
  background-color: #3884ff;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
  font-family: inherit;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  font-style: normal;
  text-align: center;
  text-decoration: none;
  color: #fff;
  cursor: pointer;
}

button[disabled] {
  pointer-events: none;
  background-color: #bdbdbd;
}

button:hover,
button:focus {
  background-color: #1f65d6;
}

.style_toast__209uI{
  background-color: #119;
  color: #fff;
}

.style_balance__GF34I {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
  color: #444;
}

span {
  margin: 10px;
}
.style_TransactionHistory__2pH9W {
  display: block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 10px;
  border-radius: 5px;
  margin: auto  ;
}

table {
  background-color: rgb(220,220,220);
}

th, td {
  padding: 15px;
  width: 200px;
  text-align: center;
}

th {
  background-color: #09f;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 80%;
}

td {
  background-color: #fff;
  color: #666;
  font-size: 90%;
}

tr:nth-child(2n) td {
  background-color: rgb(240, 240, 255);
}
*, *:after, *:before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Noto Sans JP";
}
body {
  background-color: rgb(240,240,240);
}
